.header {
    width: 100%;
    height: 90px;
    background-color: #333333;
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;

    @import '../popup/header__popup';

    .header__flex {
        display: flex;
        justify-content: space-between;
        height: 100%;

        .mobile__block {
            width: 68px;
            display: none;

            .menu__btn {
                width: 30px;
                height: 30px;
                position: relative;
                margin-right: 10px;

                span {
                    position: absolute;
                    top: 50%;
                    left: 0;
                    background: #fff;
                    width: 100%;
                    height: 2px;
                    transition: 0.2s;
                }

                span:first-child {
                    transform: translateY(-10px);
                }

                span:last-child {
                    transform: translateY(10px);
                }
            }

            .menu__btn_active {
                span:nth-of-type(2) {
                    height: 0;
                }

                span:first-child {
                    transform: rotate(45deg);
                }

                span:last-child {
                    transform: rotate(-45deg);
                }
            }

            @import "./search";
        }

        .block__first {
            width: 458px;
            display: flex;
            justify-content: space-between;
            transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);

            .logo {
                height: 100%;
                //border: solid red;

                .logo__img {
                    width: 100%;
                    height: 100%;
                    object-fit: auto;
                }
            }

            .nav {
                width: 343px;
                display: flex;
                align-items: center;
                transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);

                .nav__list {
                    width: 100%;
                    display: flex;
                    justify-content: space-between;
                    list-style-type: none;
                    align-items: center;

                    .nav__title {
                        display: flex;
                    }

                    &_link {
                        color: #fff;
                        text-decoration: none;
                        transition: all .2s;

                        &:hover {
                            color: #9d2f2f;
                            transition: all .2s;
                        }
                    }

                    .menu {
                        display: flex;
                        align-items: center;

                        span {
                            margin-right: 5px;
                        }
                    }
                }
            }
        }

        .block__first_hidden {
            display: none;
        }

        .block__second {
            //width: 548px;
            width: 650px;
            display: flex;
            align-items: center;
            margin-left: 20px;

            .phone {
                color: white;
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 18px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                width: 290px;
                margin-right: auto;
                transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
            }

            .phone__hidden {
                display: none;
                transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
            }

            @import "./search";

            .profile {
                color: white;
                display: grid;
                grid-template-columns: repeat(3, 30px);
                column-gap: 30px;
                align-items: center;

                a {
                    color: white;
                }

                .icon-search,
                .icon-profile {
                    font-size: 22px;
                    cursor: pointer;
                }

                .icon-liked,
                .icon-basket {
                    position: relative;
                    font-size: 22px;
                }

                .icon-liked_number,
                .icon-basket_number {
                    font-family: "Open Sans";
                    font-style: normal;
                    font-weight: 600;
                    font-size: 8px;
                    line-height: 11px;
                    background-color: #9d2f2f;
                    height: 12px;
                    width: 12px;
                    border-radius: 50%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    position: absolute;
                    left: 50%;
                    right: 0%;
                    top: 50%;
                    bottom: 0%;
                }
            }
        }
    }
}

.gray {
    background-color: #888 !important;
}

@import "./media/header-media";